"use client";

import { useMemo, useState } from "react";
import DateFiltersExports2 from "@/components/DateFiltersExports2";
import { FilterState } from "@/types/filters";

export default function TestPage({
    inventory,
    locations,
    categories,
    products,
}: {
    inventory: any[];
    locations: any[];
    categories: any[];
    products: any[];
}) {
    // ✅ 1. Filters live in the page
    const [filters, setFilters] = useState<FilterState>({
        startDate: null,
        endDate: null,
        locationIds: [],
        categoryIds: [],
        productIds: [],
    });

    // ✅ 2. Filter logic belongs to the page
    const filteredInventory = useMemo(() => {
        return inventory.filter((item) => {
            if (
                filters.locationIds?.length &&
                !filters.locationIds.includes(item.locationId)
            ) return false;

            if (
                filters.productIds?.length &&
                !filters.productIds.includes(item.productId)
            ) return false;

            if (
                filters.categoryIds?.length &&
                !filters.categoryIds.includes(item.product?.category)
            ) return false;

            return true;
        });
    }, [inventory, filters]);

    // ✅ 3. Export handlers are page-specific
    function exportInventoryCSV() {
        console.log("Export CSV", filteredInventory);
    }

    function exportInventoryPDF() {
        console.log("Export PDF", filteredInventory);
    }

    return (
        <div className="space-y-4">
            <h4 className="text-lg font-semibold">Test Page</h4>

            <DateFiltersExports2
                locations={locations}
                categories={categories}
                products={products}
                onChange={setFilters}
                onExportCSV={exportInventoryCSV}
                onExportPDF={exportInventoryPDF}
            />

            {/* Example usage */}
            <div className="text-sm text-gray-600">
                Showing {filteredInventory.length} items
            </div>
        </div>
    );
}